let html = document.querySelector(".html");
let style = document.querySelector('.style')
let str = `
/*
你好,
我是一名前端新人,
接下来我将画一幅阴阳图,
首先准备一个div
*/
    .div1 {
        height: 500px;
        width: 500px;
        border: 1px solid pink;
    }
   
/*
之后将div变成原形
*/
     .div1 {
        border-radius: 50%;
        border: none;
        box-shadow: 0 0 3px rgba(0, 0, 0, 10);
    }
 /*众所周知,阴阳图是阴阳两分的*/
    .div1 {
        background: linear-gradient(90deg, rgba(0, 0, 0, 1) 50%, 
        rgba(255, 255, 255, 1) 50%, rgba(255, 255, 255, 1) 100%);
    }
        .div1::before {
        content: '';
        display: block;
        position: absolute;
        width: 250px;
        height: 250px;
        left: 50%;
        transform: translateX(-50%);
        border-radius: 50%;
background: radial-gradient(circle, rgba(255,255,255,1) 24%, rgba(255,255,255,1) 24%, rgba(0,0,0,1) 24%);

        
    }

    .div1::after {
        content: '';
        display: block;
        position: absolute;
        width: 250px;
        height: 250px;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        border-radius: 50%;
        background: radial-gradient(circle, rgba(0,0,0,1) 24%, rgba(255,255,255,1) 24%, rgba(255,255,255,1) 100%);
    }
`


let str2 = "";
let n = 0;

let step = () => {
    setTimeout(() => {
        if (str[n] === "\n") {
            str2 += "<br>";
        } else if (str[n] === ' ') {
            str2 += "&nbsp;"
        } else {
            str2 += str[n];
        }
        html.innerHTML = str2;
        style.innerHTML = str.substring(0, n)
        window.scrollTo(0, 99999);
        html.scrollTo(0, 99999);
        n++;
        if (n < str.length) {
            step();
        }
    }, 0);
};
step();
